.card {
  pointer-events: none;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  padding: 20px;
  background: white;
  border-radius: 5px;
  width: 500px;
  height: 250px;
  margin: auto;
  -webkit-transform-style: preserve-3d;
  transform-style: preserve-3d;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  display: flex;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  position: relative;
}

.card:after {
  content: " ";
  position: absolute;
  width: 100%;
  height: 10px;
  border-radius: 50%;
  left: 0;
  bottom: -50px;
  box-shadow: 0 30px 20px rgba(0, 0, 0, 0.3);
}

.card .card-content {
  margin: auto;
  text-align: left;
  -webkit-transform-style: preserve-3d;
  transform-style: preserve-3d;
}

.card h1 {
  -webkit-transform: translateZ(90px);
  transform: translateZ(90px);
  font-size: 4em;
  margin-bottom: 0%;
}

.card p {
  -webkit-transform: translateZ(70px);
  transform: translateZ(70px);
  display: block;
}

.card p.sub {
  -webkit-transform: translateZ(30px);
  transform: translateZ(30px);
  font-style: italic;
}

.card a {
  color: transparent;
  pointer-events: auto;
}

.card svg:hover {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}

.noselect {
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Safari */
  /* Konqueror HTML */
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
  /* Non-prefixed version, currently
                                  supported by Chrome and Opera */
}

.home {
  display: flex;
  height: 90vh;
  font-family: "Roboto", georgia, serif;
}

.details {
  position: relative;
  z-index: 5;
  height: 250px;
  width: 100%;
  color: #000;
  padding: 20px;
}

.detail-background {
  height: 100%;
  width: 100%;
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-repeat: no-repeat;
  background-size: 100%;
  opacity: 0.2;
}

